import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'Home',
      component: function(resolve){
        require(['../components/Home.vue'], resolve)
      }
    },{
      path: '/login',
      name: 'Login',
      component: function(resolve){
        require(['../components/Login.vue'], resolve)
      }
    },{
      path: '/',
      name: 'Login',
      component: function(resolve){
        require(['../components/Login.vue'], resolve)
      }
    },{
      path: '/password',
      name: 'password',
      component: function(resolve){
        require(['../components/password.vue'], resolve)
      }
    }
  ]
})



